    onDragStart(event) {
      this.draging = event.target;
      this.dragindex1 = this._index(this.draging);
    },
    onDragOver(event) {
      this.target = event.target;
      if (this.target.nodeName === "LI" && this.target !== this.draging) {
        this.fileList[this._index(this.draging)] = this.fileList.splice(
          this._index(this.target),
          1,
          this.fileList[this._index(this.draging)]
        )[0];
      }
    },

    onDragEnd(event) {
      let currentNodes = Array.from(this.$refs.parentNode.childNodes);

      let data = currentNodes.map((i, index) => {
        let item = this.fileList.find((c) => c == i.innerText);
        return item;
      });
    },
    _index(el) {
      let domData = Array.from(this.$refs.parentNode.childNodes);
      return domData.findIndex((i) => i.innerText == el.innerText);
    },
