    // 
    async imgMoveDown(
      e,
      id = this.imgOperationData.id,
      goods_id = this.imgOperationData.goods_id,
      dir = "down"
    ) {
      let index = this.imgOperationData.index;
      let path = "";
      if (index == "0" || index + 1 >= this.ruleForm.goods_imgs.length) {
        this.warningMess("");
      } else {
        path = this.ruleForm.goods_imgs[index].path;
        this.ruleForm.goods_imgs[index].path = this.ruleForm.goods_imgs[
          index + 1
        ].path;
        this.ruleForm.goods_imgs[index + 1].path = path;
        let res = await this.api.goodsImgdown({ id, goods_id, dir });
        this.successMess("");
      }
    },
